import React, { useState, useEffect } from "react";
import * as S from "./styles";
import axios from "axios";

interface playersProps {
  nickname: string;
  userId: string;
  state: string;
}

interface MainProps {
  user: playersProps;
  setUser: (user: playersProps) => void;
  roomcode: string;
  setRoomcode: (roomcode: string) => void;
  startGame: () => void;
}

function Main({ user, setUser, roomcode, setRoomcode, startGame }: MainProps) {
  const [loading, setLoading] = useState(false);
  const [nickname, setNickname] = useState("");
  const handleNicknameChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setNickname(e.target.value);
  };
  const handleRoomcodeChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setRoomcode(e.target.value);
  };

  const makeRoom = async () => {
    axios
      .post(
        "https://realdragon.herokuapp.com/room",
        { nickname: nickname },
        { withCredentials: true }
      )
      .then(({ data }) => {
        setRoomcode(data.roomCode);
        setUser({ nickname: nickname, userId: data.userId, state: "ALIVE" });
        setLoading(true);
      })
      .then(() => {
        startGame();
      })
      .catch((e) => {
        console.error(e);
        setLoading(false);
      });
  };

  const enterRoom = async () => {
    axios
      .post(
        "https://realdragon.herokuapp.com/room/enter",
        {
          roomCode: roomcode,
          nickname: nickname,
        },
        { withCredentials: true }
      )
      .then(({ data }) => {
        setLoading(true);
        setUser({
          nickname: nickname,
          userId: data.userId,
          state: "ALIVE",
        });
      })
      .then(() => {
        startGame();
      })
      .catch((e) => {
        console.error(e);
        setLoading(false);
      });
  };
  return (
    <>
      <S.Main>
        <S.WaveContainer>
          <S.Wave url={`${process.env.PUBLIC_URL}/assets/wave.png`} />
        </S.WaveContainer>
        <S.Comment>
          <span style={{ color: "#900" }}>Î∞ï</span>Î†•ÏûàÎäî{" "}
          <span style={{ color: "#900" }}>ÏßÑ</span>Ïßú Ïñ¥Î•∏
          <span style={{ color: "#900" }}>Ïö©</span> Í≤åÏûÑ
        </S.Comment>
        <S.Title>
          ÎßàÌîºÏïÑüêâ
          <span style={{ color: "#900" }}>Î∞ïÏßÑÏö©</span>
        </S.Title>
        <S.InputContainer>
          <S.Label>ÎãâÎÑ§ÏûÑ</S.Label>
          <S.ChatInput
            type="text"
            placeholder="10Ïûê Ïù¥ÎÇ¥Î°ú ÏûÖÎ†•ÌïòÏÑ∏Ïöî"
            maxLength={10}
            onChange={handleNicknameChange}
          />
        </S.InputContainer>
        <S.InputContainer>
          <S.Label>Î∞© ÏΩîÎìú</S.Label>
          <S.ChatInput
            type="text"
            placeholder="Î∞© ÏΩîÎìúÎ•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî"
            maxLength={10}
            onChange={handleRoomcodeChange}
          />
        </S.InputContainer>
        <S.ButtonContainer>
          <S.RoomButton onClick={makeRoom}>Î∞© ÏÉùÏÑ±ÌïòÍ∏∞</S.RoomButton>
          <S.RoomButton onClick={enterRoom}>Î∞© ÏûÖÏû•ÌïòÍ∏∞</S.RoomButton>
        </S.ButtonContainer>
        <S.Credit>
          ‚ìí 2020. ÌåÄ Î∞ïÏßÑÏö© all rights reserved.
          <br />
          <span style={{ fontSize: "1.4rem", fontWeight: 500 }}>
            14 ÍπÄÏàòÌôç, 17 ÏµúÌïòÎØº, 19 ÍπÄÌòÑÏ±Ñ
          </span>
        </S.Credit>
      </S.Main>
    </>
  );
}

export default Main;
