import React, { useState } from "react";
import * as S from "./styles";
import axios from "axios";

interface PopUpProps {
  roomcode: string;
  userId: string;
  isMafia: boolean;
  keyword: string;
  isGuessing: boolean;
}

function PopUp({ roomcode, userId, isMafia, keyword, isGuessing }: PopUpProps) {
  const [chatText, setChatText] = useState("");
  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setChatText(e.target.value);
  };
  const onSubmit = async () => {
    axios
      .post(
        `https://realdragon.herokuapp.com/description`,
        { roomCode: roomcode, userId: userId, description: chatText },
        {
          withCredentials: true,
        }
      )
      .then(({ data }) => {})
      .catch((e) => {
        console.error(e);
      });
  };
  const onGuess = async () => {
    axios
      .post(
        `https://realdragon.herokuapp.com/guess`,
        { roomCode: roomcode, userId: userId, subject: chatText },
        {
          withCredentials: true,
        }
      )
      .catch((e) => {
        console.error(e);
      });
  };
  return (
    <>
      <S.Layout>
        <S.PopUp>
          {isMafia ? (
            isGuessing ? (
              <S.Text>Ï†úÏãúÏñ¥Î•º ÎßûÌòÄÏ£ºÏÑ∏Ïöî!</S.Text>
            ) : (
              <S.Text>Ï†úÏãúÏñ¥Î•º ÎààÏπòÍªè Ïú†Ï∂îÌï¥ÏÑú ÏÑ§Î™ÖÌï¥Ï£ºÏÑ∏Ïöî.</S.Text>
            )
          ) : (
            <S.Text>
              Ï†úÏãúÏñ¥ <S.KeyWord>{keyword}</S.KeyWord>ÏùÑ(Î•º) ÏÑ§Î™ÖÌï¥Ï£ºÏÑ∏Ïöî.
            </S.Text>
          )}
          <S.InputContainer>
            <S.ChatInput
              type="text"
              placeholder="10Ïûê Ïù¥ÎÇ¥Î°ú ÏûÖÎ†•ÌïòÏÑ∏Ïöî"
              maxLength={10}
              onChange={handleChange}
            />
            <S.SendButton
              isBlank={chatText.length == 0}
              onClick={isGuessing ? onGuess : onSubmit}
            >
              ÌôïÏù∏
            </S.SendButton>
          </S.InputContainer>
        </S.PopUp>
      </S.Layout>
    </>
  );
}

export default PopUp;
